* {
  box-sizing: border-box;
}

body {
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.hidden {
  display: none;
}

.search-wrap {
  background-color: #153594;
  align-items: center;
  padding: 5px;
  display: flex;
}

.search-form {
  text-align: center;
  margin: auto;
  display: flex;
}

.search-form input[type="text"] {
  height: 30px;
  border: none;
  border-radius: 5px;
  flex: 1;
  padding: 5px;
}

.search-form button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0 10px;
}

.material-symbols-outlined {
  color: #fff;
  font-size: 20px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  margin-bottom: 60px;
  display: flex;
}

.photo-card {
  border: 1px solid #ccc;
  border-radius: 3px;
  flex-direction: column;
  flex: 0 calc(25% - 20px);
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.gallery__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info {
  width: 100%;
  justify-content: space-between;
  display: flex;
}

.info-item {
  text-align: center;
  flex: 1;
}

.load-more-btn-wrap {
  position: relative;
}

.load-more {
  color: #fff;
  height: 30px;
  cursor: pointer;
  background-color: #153594;
  border: none;
  border-radius: 5px;
  margin: auto;
  padding: 5px;
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
}

.load-more:hover {
  background-color: #091a4a;
}

